	module.exports = {"data":{"challengeNode":{"title":"Use the flex-shrink Property to Shrink Items","description":["目前为止，挑战里的提到的属性都应用于 flex 容器（flex 子元素的父元素）。除此之外，flex 子元素也有很多实用属性。","首先介绍的是<code>flex-shrink</code>属性。使用之后，如果 flex 容器太小，该项目会自动缩小。当容器的宽度小于里面所有项目的宽度，项目就会自动压缩。","<code>flex-shrink</code>属性接受 number 类型的值。数值越大，与其他项目相比会被压缩得更厉害。例如，如果一个项目的<code>flex-shrink</code>为 1 ，另一个项目<code>flex-shrink</code>为 3，那么 3 的那个与另一个相比会受到 3 倍压缩。","<hr>","为<code>#box-1</code>和<code>#box-2</code>添加 CSS 属性<code>flex-shrink</code>，<code>#box-1</code>的值设为 1，<code>#box-2</code>的值设为 2。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-shrink-property-to-shrink-items","blockName":"CSS Flexbox","tests":[{"text":"<code>#box-1</code>元素应有<code>flex-shrink</code>属性，其值应为 1.","testString":"assert($('#box-1').css('flex-shrink') == '1', '<code>#box-1</code>元素应有<code>flex-shrink</code>属性，其值应为 1.');"},{"text":"<code>#box-2</code>元素应有<code>flex-shrink</code>属性，其值应为 2.","testString":"assert($('#box-2').css('flex-shrink') == '2', '<code>#box-2</code>元素应有<code>flex-shrink</code>属性，其值应为 2.');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  #box-container {\n    display: flex;\n    height: 500px;\n  }\n  #box-1 {\n    background-color: dodgerblue;\n    width: 100%;\n    height: 200px;\n    \n  }\n\n  #box-2 {\n    background-color: orangered;\n    width: 100%;\n    height: 200px;\n    \n  }\n</style>\n\n<div id=\"box-container\">\n  <div id=\"box-1\"></div>\n  <div id=\"box-2\"></div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-flexbox/use-the-flex-grow-property-to-expand-items","id":"587d78ad367417b2b2512afb"},"slug":"/responsive-web-design/css-flexbox/use-the-flex-shrink-property-to-shrink-items"}}

